import React from 'react'
import { number, oneOf, shape } from 'prop-types'
import injectSheet, { jss, ThemeProvider } from 'react-jss'
import jssNested from 'jss-nested'

jss.use(jssNested())

const getapperLogo = 'data:image/svg+xml;base64,'

const getapperLogoPositive = 'data:image/svg+xml;base64,'


const styles = (theme) => {
  const { radius, shadow } = theme
  const size = radius * 0.4
  const width = Math.max(200, radius * 2)

  return ({
    '@keyframes spin': {
      from: {
        transform: 'rotate(0deg)'
      },
      to: {
        transform: 'rotate(360deg)'
      }
    },
    '@keyframes spinback': {
      from: {
        transform: 'rotate(0deg)'
      },
      to: {
        transform: 'rotate(-360deg)'
      }
    },
    wrapper: {
      color: theme.color,
      font: {
        size: 12
      },
      textAlign: 'left',
      textDecoration: 'none',
      position: 'fixed',
      zIndex: 9999999,
      bottom: 0,
      right: -width + radius,
      height: radius,
      width,
      background: theme.background,
      filter: shadow,
      borderTopLeftRadius: radius,
      boxSizing: 'border-box',
      padding: {
        left: radius / 2.5,
        right: radius / 2.5,
        top: radius * 0.15
      },
      display: 'flex',
      alignItems: 'center',
      transition: 'all ease-out .33s',
      '&:hover': {
        right: 0,
        opacity: 0.7,
        '& $logo': {
          animation: {
            name: 'spinback',
            duration: '.5s'
          }
        }
      }
    },
    logo: {
      width: 'auto',
      height: size,
      margin: {
        right: radius * 0.3
      },
      animation: {
        name: 'spin',
        duration: '.5s'
      }
    }
  })
}

const Comp = ({ classes, themeType }) => (
  <a
    className={classes.wrapper}
    href="http://getapper.com"
    target="_blank"
    rel="noopener noreferrer"
  >
    <img
      alt="Getapper"
      className={classes.logo}
      src={themeType === 'light' ? getapperLogo : getapperLogoPositive}
    />
    <span>made by getapper</span>
  </a>
)

Comp.propTypes = {
  classes: shape({}).isRequired,
  themeType: oneOf(['light', 'dark']).isRequired
}

const StyledComp = injectSheet(styles)(Comp)

const theme = (radius, isLight) => ({
  background: isLight ? '#fff' : '#222',
  color: isLight ? '#24292e' : '#fff',
  shadow: isLight ? 'drop-shadow(0px 0px 5px rgba(0,0,0,.3))' : 'drop-shadow(0px 0px 5px rgba(0,0,0,.8))',
  radius
})

const App = ({
  radius,
  themeType
}) => (
  <ThemeProvider theme={theme(radius, themeType === 'light')}>
    <StyledComp themeType={themeType} />
  </ThemeProvider>
)

App.propTypes = {
  radius: number,
  themeType: oneOf(['light', 'dark'])
}

App.defaultProps = {
  radius: 75,
  themeType: 'light'
}

export default App



